/*
 * rules for style
 */

.text-center {
  text-align: center;
}
.button-link {
  color: white;
  text-decoration: none;
}


/*
 * rules for layout
 */

body {
  padding-top: 70px;
}
.flex-box {
  display: flex;
  justify-content: space-around;
}
.margin-bottom {
  margin-bottom: 10px;
}
.margin-top-md {
  margin-top: 50px;
}
.no-padding-left {
  padding-left: 0;
}


/*
 * rules facilities
 */

.jumbotron {
  .text-center;
}

.styled-list li {
  list-style:none;
  .no-padding-left;
}
.styled-list li:before {
  .no-padding-left;
  content: "";
  display:inline-block;
  width: 0.7em;
  height: 0.7em;
  border-radius: 50%;
  background-color: pink;
  margin-right: 3px;
}
